import { Box, Input, FormLabel } from '@chakra-ui/react'
import React, { useContext, useEffect } from 'react'
import { useTable, usePagination, useSortBy, useGlobalFilter } from 'react-table'
import { StyledPaginationButton as StyledButton } from 'utils/StyledButton'
import { HiDocumentText } from 'react-icons/hi'
import StyledFormLabel from 'utils/StyledFormLabel'
import styled from 'styled-components'
import { FaTrashAlt } from 'react-icons/fa'
import { FormSubmitButton } from 'utils/StyledButton'
const StyledBox = styled(Box)`
	border-bottom: 1px solid;
	height: 1.8em;
	min-width: 160px;
	padding: 0 1em;
	border-color: black;
`

export default function AddTransferSheet() {
	const data = React.useMemo(
		() => [
			{
				merchantNumber: '2021/01/05',
				merchantName: '1100101-001',
				note: 'OPMM00jirjgoegjioerjgiorejiorgjeoi1',
				positionBefore: 'UHFSODHIFJOJ',
				condition: 'jiewfjowf',
				purchasePrice: '1000',
				sellPrice: '2000',
				billingStatement: 'no',
			},
		],
		[]
	)

	const columns = React.useMemo(
		() => [
			{
				Header: '',
				accessor: 'merchantNumber',
			},
			{
				Header: '',
				accessor: 'merchantName',
			},
			{
				Header: '',
				accessor: 'note',
				disableGlobalFilter: true,
			},

			{
				Header: '',
				accessor: 'positionBefore',
				disableGlobalFilter: true,
			},
			{
				Header: '',
				accessor: 'condition',
				disableGlobalFilter: true,
			},
			{
				Header: '',
				accessor: 'purchasePrice',
				disableGlobalFilter: true,
			},
			{
				Header: '',
				accessor: 'sellPrice',
				disableGlobalFilter: true,
			},
			{
				Header: '',
				accessor: 'billingStatement',
				disableGlobalFilter: true,
			},
			{
				Header: '',
				accessor: 'management',
				disableGlobalFilter: true,
				Cell: (row) => {
					return (
						<Box d="flex" justifyContent="space-around">
							<Box color="#EB9191" cursor="pointer">
								<FaTrashAlt />
							</Box>
						</Box>
					)
				},
			},
		],
		[]
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		page,
		prepareRow,
		setGlobalFilter,
		pagination,
		canPreviousPage,
		canNextPage,
		pageOptions,
		pageCount,
		gotoPage,
		nextPage,
		previousPage,
		state: { pageIndex },
	} = useTable(
		{
			columns,
			data,
			initialState: { pageIndex: 0, pageSize: 10 },
		},
		useGlobalFilter,
		useSortBy,
		usePagination
	)
	return (
		<>
			<Box d="flex" justifyContent="space-between" mb="1.5em">
				<form>
					<StyledFormLabel tag="" />
					<Box d="flex">
						<Input placeholder="" maxW="170px" mr="1em" />
						<FormSubmitButton></FormSubmitButton>
					</Box>
				</form>
				<Box d="flex">
					<Box mr="2em">
						<FormLabel></FormLabel>
						<StyledBox color="gray.600">()</StyledBox>
					</Box>
					<Box mr="2em">
						<FormLabel></FormLabel>
						<StyledBox color="gray.600">(OPMM001)</StyledBox>
					</Box>
				</Box>
			</Box>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map((headerGroup, g) => (
						<tr {...headerGroup.getHeaderGroupProps()} key={g}>
							{headerGroup.headers.map((column, k) => (
								<th key={k}>
									<Box {...column.getHeaderProps(column.getSortByToggleProps())}>
										{
											// Render the header
											column.render('Header')
										}
									</Box>
								</th>
							))}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{
						// Loop over the table rows
						page.map((row, i) => {
							// Prepare the row for display
							prepareRow(row)
							return (
								// Apply the row props
								<tr {...row.getRowProps()} key={i}>
									{
										// Loop over the rows cells
										row.cells.map((cell, j) => {
											// console.log(cell.column.Header)
											// Apply the cell props
											return (
												<td {...cell.getCellProps()} key={j}>
													{
														// Render the cell contents
														cell.render('Cell')
													}
												</td>
											)
										})
									}
								</tr>
							)
						})
					}
				</tbody>
			</table>
			<Box d="flex" justifyContent="center" alignItems="center" pt="1em">
				<StyledButton onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
					{'<<'}
				</StyledButton>
				<StyledButton onClick={() => previousPage()} disabled={!canPreviousPage}>
					{'<'}
				</StyledButton>
				<Box color="#2250a6" fontSize="1.3em" mx=".5em">
					{pageIndex + 1}
				</Box>
				<StyledButton onClick={() => nextPage()} disabled={!canNextPage}>
					{'>'}
				</StyledButton>
				<StyledButton onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
					{'>>'}
				</StyledButton>
			</Box>
		</>
	)
}
