import { Box, Input, InputGroup, InputRightElement, FormLabel, Text } from '@chakra-ui/react'

import styled, { keyframes } from 'styled-components'
import { useForm } from 'react-hook-form'
import { vendorContext } from './useContext/createContext'
import { useContext, useState } from 'react'
import { AiOutlineSearch, AiOutlinePlus } from 'react-icons/ai'
import { FormSubmitButton } from 'utils/StyledButton'
import { FaPlus } from 'react-icons/fa'
import SingleItem from './component/SingleItem'
import DisplayTable from './component/DisplayTable'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledTransitionBg = styled(Box)`
	width: 100%;
	animation: ${fadeInRight} 0.5s ease;
`

const StyledForm = styled.form`
	width: 100%;
`

const merchantItemData = {
	merchantID: '',
	merchantName: '',
}

export default function SubContent({ setEditStatus }) {
	const { setEditState, editState } = useContext(vendorContext)
	const { register, handleSubmit } = useForm()
	const [merchantListData, setMerchantListData] = useState([merchantItemData])

	const newMerchant = () => {
		setMerchantListData((pre) => pre.concat(merchantItemData))
	}

	const submitHandler = (e) => {
		setEditState((pre) => (pre = { ...pre, step: 2 }))
	}
	return (
		<StyledTransitionBg>
			{/* TODO: FORMFOR LOOPARRAY */}
			<StyledForm onSubmit={handleSubmit((e) => submitHandler(merchantListData))}>
				{merchantListData.map((item, i) => {
					return <SingleItem register={register} order={i} key={i} setMerchantListData={setMerchantListData} merchantListData={merchantListData} />
				})}
				<Box textAlign="center" py="1em">
					<FormSubmitButton mx=".5em" onClick={() => setEditState((pre) => (pre = { ...pre, step: 'table' }))}>
						
					</FormSubmitButton>
					<FormSubmitButton mx=".5em" type="submit">
						
					</FormSubmitButton>
				</Box>
			</StyledForm>
			{editState.step === 'edit' ? <DisplayTable /> : null}
		</StyledTransitionBg>
	)
}
